(function() {
    'use strict';

    angular
        .module('frontend')
        .service('dataService', dataService);

    /** @ngInject */
    function dataService($http, $q, $log, $timeout, $location, $rootScope) {

        // var serialize = function(obj, prefix) {
        //   var str = [];
        //   for(var p in obj) {
        //     if (obj.hasOwnProperty(p)) {
        //       var k = prefix ? prefix + "[" + p + "]" : p, v = obj[p];
        //       str.push(typeof v == "object" ?
        //         serialize(v, k) :
        //         encodeURIComponent(k) + "=" + encodeURIComponent(v));
        //     }
        //   }
        //   return str.join("&");
        // }

        return {
            // User Data
            getData: function(times, days){

                var deferred = $q.defer();

                var queryStr = 'https://3zstizfpne.execute-api.us-west-2.amazonaws.com/prod/records?start=' + times.start + '&end=' + times.end + '&days=' + days;

                // Uncomment for testing
                // $http.get('app/components/dataServices/data.json')
                $http.get(queryStr) // comment out for testing

                .then(function(data) {
                    deferred.resolve(data);
                }).catch(function(error) {
                    $log.error(error);
                    deferred.resolve(null);
                });

                return deferred.promise;
            }
        };
    }
})();
(function() {
    'use strict';

    angular
        .module('frontend')
        .controller('MainController', MainController);

    /** @ngInject */
    function MainController($scope, dataService, NgMap) {

        var mapData, vm = this;
        $scope.mapData = [];
        $scope.possibleTimes = ['12 am', '1 am','2 am','3 am','4 am','5 am','6 am','7 am','8 am','9 am','10 am','11 am','12 pm','1 pm','2 pm','3 pm','4 pm','5 pm','6 pm','7 pm','8 pm','9 pm','10 pm','11 pm']
        $scope.times = {
            start: '6 am',
            end: '9 am'
        }

        $scope.lateness = 0;

        $scope.$on('mapInitialized', function (event, map) {
           $scope.busTimesMap = map;
        });

        // Multi-select params
        $scope.selectedRoutes = [];
        $scope.routes = [];
        $scope.routesIds = [];
        $scope.mOptions = {
            enableSearch: true,
            scrollable: true,
            scrollableHeight: '500px'
        }
        $scope.mTexts = {
            buttonDefaultText: 'Select Route(s)'
        }

        // days of week button
        $scope.days = ["Weekdays", "Weekends","Sunday","Monday", "Tuesday","Wednesday","Thursday","Friday", "Saturday"];
        $scope.day = "Weekdays";

        function getData(){

            $scope.loading = true;
            dataService.getData($scope.times, $scope.day).then(function(data){
                $scope.loading = false;
                $scope.data = data.data;

                // Process points
                for (var point in $scope.data){
                    if ($scope.routesIds.indexOf($scope.data[point].routeid) === -1){
                        $scope.routesIds.push($scope.data[point].routeid);
                    }
                }

                // process routes
                $scope.routes = [];
                for (var i in $scope.routesIds){
                    $scope.routes.push({
                        id: $scope.routesIds[i],
                        label: $scope.routesIds[i]
                    });
                }
                processData();
            });
        }

        function processData(){

            $scope.mapData = [];

            for (var point in $scope.data){
                var weight = parseInt($scope.data[point].stopdeviationinseconds);

                if ($scope.lateness < (weight / 60)){

                    // if point in route ids
                    for (var i in $scope.selectedRoutes){
                        if ($scope.selectedRoutes[i].id === $scope.data[point].routeid){
                            $scope.mapData.push({
                                location: new google.maps.LatLng($scope.data[point].lat, $scope.data[point].lon),
                                weight: weight / 60,
                                lat: $scope.data[point].lat,
                                lon: $scope.data[point].lon,
                                id: $scope.data[point].routeid
                            });
                        }
                    }
                }
            }

            vm.ready = true;
        }

        $scope.$watch('selectedRoutes', function(newVal, oldVal) {
            if (newVal !== oldVal){
                processData();
            }
        }, true);

        $scope.compute = function(){
            var start = parseInt($scope.times.start.match(/\d+/))
            var end = parseInt($scope.times.end.match(/\d+/))

            // convert to military time
            if ($scope.times.start.indexOf('pm') != -1){
                start += 12;
            }
            if ($scope.times.end.indexOf('pm') != -1){
                end += 12;
            }

            if (start > end){
                $scope.badTime = true;
            } else {
                $scope.badTime = false;
                getData();
            }
        }

        $scope.$watch('lateness', function(newVal, oldVal) {
            if (newVal !== oldVal){
                processData();
            }
        });

        // map marker popups
        $scope.showInfoWindow = function (event, p) {
            var infowindow = new google.maps.InfoWindow();
            var center = new google.maps.LatLng(p.lat,p.lon);
            infowindow.setContent('<div><span><b>Route:  </b>' + p.id + '</span><br/>' + '<span><b>Late(median):</b>  ' + p.weight * 60 + ' sec</span><br/>' + '<span><b>Lat/Lon:</b>  ' + p.lat + ' ' + p.lon + '</span><br/></div>');
            infowindow.setPosition(center);
            infowindow.open($scope.busTimesMap);
         };

         // init
         getData()

    }
})();

(function() {
    'use strict';

    angular.module('customFilters', [])

    .filter('mn', function() {
        return function(input) {
            return Number(input.substring(0, input.length - 2));
        };
    })

    .filter('sizer', function() {
        return function(obj) {
            var size = 0,
                key;
            for (key in obj) {
                if (obj.hasOwnProperty(key)) {
                    size++;
                }
            }
            return size;
        };
    })

    .filter('getType', function() {
        return function(rdd) {
            if (rdd) {
                if (rdd.indexOf('ShardMapRDD') !== -1) {
                    return 'Shard Map';
                } else if (rdd.indexOf('MapRDD') !== -1) {
                    return 'Map';
                } else if (rdd.indexOf('ShardReduceRDD') !== -1) {
                    return 'Shard Reduce';
                } else if (rdd.indexOf('FilterRDD') !== -1) {
                    return 'Filter';
                } else if (rdd.indexOf('SliceRDD') !== -1) {
                    return 'SliceRDD';
                }
            } else {
                return 'Idle';
            }

        };
    })

    .filter('getDate', function() {
        return function(ms) {
            return (new Date(ms)).toString();
        };
    })

    .filter('bytesToMB', function() {
        return function(bytes) {
            return parseFloat(bytes / 1024 / 1024).toFixed(2);
        };
    })

    .filter('encode', function() {
        return function(str) {
            return encodeURIComponent(str);
        };
    })

    .filter('shortenStr', function() {
        return function(str, length) {
            return str.substring(0, length) + '...';
        };
    })

    .filter('isDuplicate', function() {
        return function(input, id) {
            var i;
            for (i in input) {
                if (input[i].id === id) {
                    return true;
                }
            }
            return false;
        };
    })

    .filter('millisToTime', function() {
        return function(num) {
                
            var times = {
                d: 86400000,
                h: 3600000,
                m: 60000,
                s: 1000,
                ms: 1
            }, newDate = '';

            for (var t in times){
                if (num / times[t] >= 1){
                    var count = Math.floor(num / times[t]);
                    num = num - (count * times[t]);
                    newDate += count + t + ' ';
                }
            }

            return newDate;
        };
    })

    // Execution Server
    .filter('toArray', function() {
        return function(obj, addKey) {
            if (addKey === false) {
                return Object.keys(obj).map(function(key) {
                    return obj[key];
                });
            } else {
                return Object.keys(obj).map(function(key) {
                    if (typeof obj[key] === 'object') {
                        return Object.defineProperty(obj[key], '$key', {
                            enumerable: false,
                            value: key
                        });
                    }
                });
            }
        };
    })

    .filter('alphaNum', function() {
        return function(input) {
            return input.replace(/\W/g, '');
        };
    })

    .filter('secondsToTimeString', function() {
        return function(millseconds) {

            if (millseconds === '0') {
                return 'All Time';
            } else if (millseconds === '60') {
                return '1 min';
            } else if (millseconds === '600') {
                return '10 min';
            } else if (millseconds === '3600') {
                return '1 hour';
            }

        };
    })

    .filter('unique', function() {
        return function (arr, field) {
            return _.uniq(arr, function(a) { return a[field]; });
        };
    });

})();
